// components/login-form.tsx  ────────────────────────────────────────────────
"use client";

import { useState, useEffect, type ChangeEvent, type FormEvent } from "react";
import { useRouter } from "next/navigation";
import { Card, CardContent } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { useAuth } from "@/context/AuthContext";
import type { User } from "@/context/types";
import Image from "next/image";
import { Button } from "@/components/ui/button";

/* -------------------------------------------------------------------------- */
/*                                   Utils                                    */
/* -------------------------------------------------------------------------- */

/** Obtiene los usuarios almacenados en _localStorage_.*/
const getUsers = (): User[] =>
    JSON.parse(localStorage.getItem("kwikemart_users") || "[]");

/* -------------------------------------------------------------------------- */
/*                               Tipos locales                                */
/* -------------------------------------------------------------------------- */

interface FormState {
    identifier: string;
    password: string;
}

/* -------------------------------------------------------------------------- */
/*                           Componente principal                             */
/* -------------------------------------------------------------------------- */

/**
 * **LoginForm**
 *
 * - Valida email + contraseña mínima de 6 caracteres.
 * - Autentica contra los usuarios guardados en `localStorage`.
 * - Muestra mensajes de éxito / error y redirige al _home_ cuando el inicio de
 *   sesión es correcto.
 */
export default function LoginForm() {
    const { login, isAuthenticated } = useAuth();
    const router = useRouter();

    const [form, setForm] = useState<FormState>({
        identifier: "",
        password: "",
    });
    const [error, setError] = useState("");
    const [success, setSuccess] = useState("");

    /* --------------------------- Manejadores --------------------------- */

    const handleChange = (e: ChangeEvent<HTMLInputElement>) => {
        const { name, value } = e.target;
        setForm((prev) => ({ ...prev, [name]: value }));
    };

    const handleSubmit = (e: FormEvent) => {
        e.preventDefault();
        setError("");
        setSuccess("");

        const { identifier, password } = form;

        // Validación rápida de inputs
        if (!identifier.trim() || password.length < 6) {
            setError(
                "Por favor ingresa un correo válido y contraseña (mínimo 6 caracteres)."
            );
            return;
        }

        // Autenticación (mock) contra localStorage
        const users = getUsers();
        const user = users.find(
            (u) => u.email === identifier && u.password === password
        );

        if (user) {
            login(user);
            setSuccess(`¡Bienvenido, ${user.username}!`);
            setForm({ identifier: "", password: "" });
        } else {
            setError("Credenciales inválidas");
        }
    };

    /* ----------------------- Efectos secundarios ----------------------- */

    // Cuando el usuario ya está logueado, redirige al home tras 700 ms.
    useEffect(() => {
        if (isAuthenticated) {
            const t = setTimeout(() => router.push("/"), 700);
            return () => clearTimeout(t);
        }
    }, [isAuthenticated, router]);

    /* ------------------------- Renderizado UI -------------------------- */

    return (
        <div className="flex flex-col gap-6">
            <Card className="overflow-hidden p-0">
                <CardContent className="grid p-0 md:grid-cols-2">
                    {/* ---------- Formulario ---------- */}
                    <form onSubmit={handleSubmit} className="p-6 md:p-8">
                        <div className="flex flex-col gap-6">
                            {/* Encabezado */}
                            <div className="flex flex-col items-center text-center">
                                <h1 className="text-2xl font-bold text-yellow-600">
                                    ¡Bienvenido de nuevo al Kwik-E-Mart!
                                </h1>
                                <p className="text-sm text-muted-foreground">
                                    Inicia sesión para llenar tu carrito con
                                    ofertas de Springfield 😎
                                </p>
                            </div>

                            {/* Email */}
                            <div className="grid gap-2">
                                <Label htmlFor="identifier">
                                    Correo electrónico
                                </Label>
                                <Input
                                    id="identifier"
                                    name="identifier"
                                    type="email"
                                    placeholder="apu@kwikemart.com"
                                    value={form.identifier}
                                    onChange={handleChange}
                                    required
                                />
                            </div>

                            {/* Password */}
                            <div className="grid gap-2">
                                <div className="flex items-center">
                                    <Label htmlFor="password">Contraseña</Label>
                                    <a
                                        href="#"
                                        className="ml-auto text-sm underline-offset-2 hover:underline">
                                        ¿Olvidaste tu contraseña?
                                    </a>
                                </div>
                                <Input
                                    id="password"
                                    name="password"
                                    type="password"
                                    placeholder="mínimo 6 caracteres"
                                    value={form.password}
                                    onChange={handleChange}
                                    required
                                    minLength={6}
                                />
                            </div>

                            {/* Botón login */}
                            <Button
                                type="submit"
                                className="w-full bg-yellow-500 hover:bg-yellow-600">
                                Iniciar sesión
                            </Button>

                            {/* Enlace a registro */}
                            <div className="text-center text-sm">
                                ¿No tienes cuenta?{" "}
                                <a
                                    href="/registro"
                                    className="underline underline-offset-4">
                                    Regístrate
                                </a>
                            </div>

                            {/* Estado */}
                            {error && (
                                <p className="text-sm text-red-600 font-medium">
                                    {error}
                                </p>
                            )}
                            {success && (
                                <p className="text-sm text-green-600 font-medium">
                                    {success}
                                </p>
                            )}
                        </div>
                    </form>

                    {/* ---------- Imagen lateral ---------- */}
                    <div className="bg-muted relative hidden md:block">
                        <Image
                            src="/images/banner2.jpg"
                            alt="Interior del Kwik-E-Mart"
                            className="absolute inset-0 h-full w-full object-cover brightness-90"
                            fill
                            priority
                        />
                    </div>
                </CardContent>
            </Card>

            {/* ---------- Términos ---------- */}
            <div className="dark:text-muted-foreground text-center text-xs *:[a]:underline *:[a]:underline-offset-4">
                Al hacer clic en “Iniciar sesión”, aceptas nuestros{" "}
                <a href="#">Términos del Kwik-E-Mart</a> y{" "}
                <a href="/politica">Política de Privacidad</a>.
            </div>
        </div>
    );
}
